@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
*{box-sizing:border-box}body{margin:0;font-family:"Muli", sans-serif;overflow:hidden}

.good-boys{background:#1d1d1d;height:100vh;display:flex;justify-content:center;align-items:center}.container{width:100%;display:flex;padding:0 20px}.slide{position:relative;height:80vh;border-radius:20px;margin:10px;cursor:pointer;color:#fff;flex:1 1;background-size:cover;background-position:center;background-repeat:no-repeat;transition:all 0.5s ease-in-out}.slide h3{position:absolute;font-size:24px;bottom:20px;left:20px;margin:0;opacity:0}.slide.active{flex:10 1}.slide.remove{flex:0.000000001 1;width:0;margin:0}.slide.add{animation:fadein 0.5s}.slide.active h3{opacity:1;transition:opacity 0.3s ease-in 0.4s}@keyframes fadein{from{flex:0.000000001 1}to{flex:1 1}}

